<template>
	<view class="page bgwhite">
		<view class="gaugeWrap">
			<qiun-data-charts type="gauge" :opts="opts" :chartData="chartData" canvas2d />
		</view>
		<view class="radius btn oee">OEE/{{ oeeName }}</view>
	</view>
</template>

<script>

export default {
	data() {
		return {
            oeeName: null,
			chartData: {},
			opts: {
				color: ["#1890FF", "#91CB74", "#FAC858", "#EE6666", "#73C0DE", "#3CA272", "#FC8452", "#9A60B4",
					"#ea7ccc"
				],
				padding: undefined,
				title: {
					name: "0%",
					fontSize: 20,
					color: "#2fc25b",
					offsetY: 50
				},
				subtitle: {
					name: undefined,
					fontSize: 15,
					color: "#666666",
					offsetY: -50
				},
				extra: {
					gauge: {
						type: "default",
						width: 10,
						labelColor: "#666666",
						startAngle: 0.75,
						endAngle: 0.25,
						startNumber: 0,
						endNumber: 100,
						labelFormat: "",
						splitLine: {
							fixRadius: 0,
							splitNumber: 10,
							width: 20,
							color: "#fff",
							childNumber: 5,
							childWidth: 10
						},
						pointer: {
							width: 14,
							color: "auto"
						}
					}
				}
			}
		};
	},
	onLoad(options) {
		console.log("OEE传过来的数据=", options.value , options.name)
        uni.setNavigationBarTitle({ // 修改头部标题
			title: options.name
		});

        this.oeeName = options.name
        this.getServerData(options.value)
	},
	methods: {
		getServerData(data , name) {
			if (data != null) {
				setTimeout(() => {
					//模拟服务器返回数据，如果数据格式和标准格式不同，需自行按下面的格式拼接
					let res = {
						categories: [{
							"value": 0.2,
							"color": "#228b22"
						}, {
							"value": 0.8,
							"color": "#4488bb"
						}, {
							"value": 1,
							"color": "#ff4500"
						}],
						series: [{
							name: "OEE/" + name,
							data: data
						}]
					};
					this.chartData = JSON.parse(JSON.stringify(res));
					this.opts.title.name = Number(data*100).toFixed(1) + "%"
				}, 500);
			}
		}
	}
}
</script>

<style lang="scss" scoped>
.gaugeWrap {
	width: 100%;
	height: 430upx;
	padding-top: 30upx;
}

.btn {
	background-color: #eac80d;
	text-shadow: 0px 1px 3px rgba(80, 39, 181, 0.75);
	color: #fff;
}

.oee {
	width: auto;
	height: 80%;
	line-height: 94upx;
	text-align: center;
	font-size: 36upx;
	margin-left: 50upx;
    margin-right: 50upx;
}

.radius {
	border-radius: 20upx;
}

</style>